@use 'variaveis.scss';
@use 'rules';

@font-face {
    font-family: 'grafite';
    src: url(../../assets/fonts/docallismeonstreet.otf) format('opentype');
}


.header {
    background-color: variaveis.$corDeFundo;

    &__container {
        
        h1 {
            color: variaveis.$corDoTitulo;
            font-family: 'grafite', sans-serif;
            font-size: 40px;
            padding: 6px;
            border: solid 0px black;
            border-radius: 4px;
        }
    }

    .nav {
        display: block;

        &__links {
            margin: 0px auto;

            &__item {
                text-align: center;
            }
        }
    }
}

.main {

    &__articles {

        h2 {
            width: 100%;
            padding: 10px;
            margin: 10px;
            border-radius: 4px;
            background: linear-gradient(to right, variaveis.$corDeFundo, transparent);
            color: variaveis.$corDoSubTitulo;
        }

        h3 {
            width: 100%;
            padding: 10px;
            margin: 10px;
            color: variaveis.$corDoSubTitulo;
            text-decoration: underline;
        }

        p {
            margin-left: 20px;
            text-indent: 10px;
            font-family: Arial, Helvetica, sans-serif;
        }

        a {
            text-decoration: none;
            color: variaveis.$corDosLinks;
        }

        small {
            margin-left: 18px;
        }

        &__item {
            margin-top: 44px;

            img {
                width: 200px;
                display: block;
                margin: 0px auto;
            }
        }
    }
}

.footer {

    &__container {

        h3 {
            color: variaveis.$corDoSubTitulo;
            text-align: center;
            margin: 24px 0px;
        }

        p {
            text-align: center;

        }

        &__cadastro {

        }
    }
}